	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/2/galaxy-vs-android-at-the-superbowl/index.md absPath of file >>> MarkdownRemark","html":"<p>The Galaxy brand is fast becoming more popular than Android, I wonder if Google feel threatened by this or if it’s part of their master plan.</p>\n<p>The most interesting part of the article is towards then end:</p>\n<blockquote>\n<p>You can see this very clearly in the new Samsung Superbowl ad. This has got lots of attention by (depending on your point of view) mocking Apple for trying to patent basic concepts or brazenly flaunting Samsung’s systematic copying of Apple’s innovations. But to me, what’s striking is that it doesn’t show the phones or any features, let alone mention Android.</p>\n<p>The subtext, of course, is: ‘the UX is a commodity, the apps are a commodity, the stuff Apple talks about is a commodity that’s absurd to patent - we’re going to compete on hardware features’. That’s a natural angle for a hardware company with no control over the platform, of course. For now.</p>\n</blockquote>","frontmatter":{"title":"Galaxy vs. Android at the Superbowl","date":"February 04, 2013","passthroughUrl":"http://ben-evans.com/benedictevans/2013/2/3/galaxy-versus-android"}}},"pathContext":{"slug":"/blog/2013/2/galaxy-vs-android-at-the-superbowl/","previous":{"fields":{"slug":"/blog/2013/2/berners-lee/"},"frontmatter":{"title":"Tim Berners-Lee on the future of the web","passthroughUrl":null,"type":null}},"next":{"fields":{"slug":"/blog/2013/2/analysis-of-the-first-ios-6-jailbreak-exploit/"},"frontmatter":{"title":"Analysis of the first iOS 6 jailbreak exploit","passthroughUrl":"http://blog.accuvantlabs.com/blog/bthomas/evasi0n-jailbreaks-userland-component","type":null}}}}

